.dropdown {
    margin: 24px 0;
}

.dropdown label {
    display: block;
    margin-bottom: 8px;
    font-size: 16px;
}

.dropdown select {
    background-color: #fff;
    box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.06);
    width: 100%;
    border: none;
    font-size: 16px;
    padding: 8px;
    box-sizing: border-box;
}